@import url('https://fonts.googleapis.com/css2?family=Noto+Kufi+Arabic:wght@700;900&family=Readex+Pro:wght@300;400;700&family=Roboto:ital,wght@0,700;1,400&display=swap');
*
{
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
  font-family: 'Readex Pro', sans-serif;
}
.flex{
  display: flex;
}
.f-c
{
  flex-direction: column;
}
.f-r
{
  flex-direction: row;
}
.f-jc
{
  justify-content: center;
}
.f-jsb
{
  justify-content: space-between;
}
.f-jsc
{
  justify-content: center;
}
.f-alc
{
  align-items: center;
}
.main
{
  color: var(--main);
}
.w-20
{
  width: 30%;
  object-fit: contain;
}
.padd-b
{
  padding-bottom: 0 !important;
}
:root
{
  --main: #D06224;
  --comman: #2F2E41;
}
body
{
  overflow-x: hidden;

}
.container
{
  width: 80%;
  margin: auto;
  color: var(--comman);
  position: relative;
}
.logo
{
  font-weight: bold;
  font-size: 47px;
  cursor: pointer;
}
.nav
{
  color: var(--main);
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}
.navbar
{
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.navbar li
{
  padding: 10px 20px;
  cursor: pointer;
  transition: 0.6s transform;
}
.navbar li:hover
{
  transform: scale(1.1) !important;
}
.loginbtn, .btn
{
  background-color: var(--main);
  color: #fff;
  border-radius: 25px;
  border: none;
  transition: 0.3s background;
}
.hero
{
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: row;
}
.hero-content
{
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-bottom: 140px;
}
.hero-content h3, .hero-content h2
{
  padding: 10px 0;
}
.hero-content h2
{
  font-size: 64px;
}
.hero-content h3
{
  font-size: 44px;
}
.hero-content span
{
  color: var(--main);
}
.input
{
  width: 90%;
  border-radius: 28px;
  height: 63px;
  padding: 30px 45px;
  font-size: 16px;
  margin: 20px 0;
  border: 1px solid var(--comman);

}
.hero-content button, .searchbtn
{
  cursor: pointer;
  width: 150px;
  height: 41px;
}
.art svg {
  position: absolute;
  left: -35vw;
  top: 18vh;
  width: 88%;
  height: 108vh;
}
.secTitle
{
  font-size: 40px;
  padding: 80px 0;
}
.card
{
  width: 50%;
  transition: 0.5s box-shadow;
}

.card .textcon
{
  width: 400px;
  padding: 0 20px;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  
}
.card .textcon h3
{
  font-size: 30px;
  padding: 15px 0;
}
.card .textcon p
{
  padding: 5px 0;
}
.imgcon
{
  width: 40%;
}
.imgcon img
{
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 10px;
  border-bottom-right-radius: 40px;
}
.textcon p.main
{
  font-size: 1.1em;
  cursor: pointer;
  text-decoration: underline var(--main);
}

.leftcon
{
  width: 50%;

}
.map
{
  transition: 0.5s transform;

}
.map img
{
  box-shadow: #2f2e41 0px 20px 16px 0px;
}
.map:hover
{
  transform: scale(1.0111111);
}
.features
{
  padding: 70px 0;
}
footer .taw
{
  text-align: center;
  color: #fff;
}
.feature img {
  width: 50%;
  transition: 0.5s transform;
}
.feature img:hover
{
  transform: scale(1.111);
}
.feature
{
  font-size: 18px;
  font-weight: bold;
}
.feature h3
{
  padding: 30px 0;
}
.cards
{
  padding: 100px 0;
}
.contactGrid
{
  width: 70%;
  justify-content: center;
  display: grid;
  margin: auto;
  grid-gap: 15px;
  grid-template-columns: 40% 40%;
  grid-template-areas: "small small2" "wide wide" "wide2 wide2" "wide3 wide3";
  padding-top: 50px;
}
.contactGrid > .inputContainer > input
{
  margin: 0 !important;
  width: 100%;
  
}
.inputContainer>svg
{
  transition: 0.5s transform;
}
.inputContainer:hover svg
{
  transform: scale(1.211);
}
.contactGrid > #username
{
  grid-area: small;
}
input#email
{
  grid-area: small2;

}
.contactGrid > #address
{
  grid-area: wide;

}
.footer-grid
{
  padding-left: 100px;
}
.contactGrid > textarea
{
  grid-area: wide2;
  height: auto;
  width: auto;
}
.contactGrid > input.input
{
  width: auto;
  margin: 0;
}
.contactGrid > button
{
  grid-area: wide3;
  margin: auto;
}
.footer
{
  margin-top: 60px;
  background-color: var(--comman);

  width: 100%;
}
.footcontainer
{
  display: grid;
  width: 80%;
  color: #fff;
  grid-template-areas: "foot foot foot foot";
  padding: 50px;
  margin: auto;
}
.footer p
{
  font-weight: lighter;
  padding: 5px 0;
}
.download
{
  width: 100%;
  display: grid;
}
.download img
{
  width: 20%;
  margin: auto;
}
.grid-c-4
{
  grid-column: 4;
}
.inputIcon
{
  position: absolute;
  left: 0;
  top: 0;
}
.btn:hover
{
  background-color: var(--comman);
}
.inputContainer
{
  position: relative;
  width: 100%;
}
.inputIcon {
  height: 100%;
  right: 45px;
}
.xn
{
  width: 75% !important;
}
.footer-social
{
  text-align: center;
}
.footer-text
{
  cursor: pointer;
  transition: 0.3s color;
}
.footer-text:hover
{
  color: var(--main);
}
.footer-grid svg {
  transform: scale(2.5);
  margin: 25px;
  cursor: pointer;
}
.footer-grid svg:hover path{
fill: var(--main);
}
.burgerMenu
{
  display: none;
}

@media (max-width: 900px) {
  .burgerMenu
{
  display: block;
}
  html
  {
    overflow-x: hidden;
  }
  .nav, .navbar
  {
    flex-direction: column;
    align-items: flex-start;
    justify-content:flex-start
  }
  .navbar
  {
    display: none;
  }
  .hero
  {
    height: 50vh;
  }
  .input
  {
    font-size: 12px;
  }
  .secTitle
  {
    padding: 0;
    font-size: 25px;
  }
  .hero-content
  {
    justify-content: flex-start;
    width: 100%;
    margin: 0;
  }
  .hero-content h2
  {
    font-size: 30px;
  }
  .hero-content h3
  {
    font-size: 20px;
  }
  .hero svg
  {
    display: none;
  }
  .features
  {
    flex-direction: column;
    font-size: 14.5px;
  }
  .feature img
  {
    transform: scale(0.9);
  }
  .feature h3
  {
    padding: 20px 0;
  }
  .resto-sec
  {
    flex-direction: column !important;
  }
  .map img
  {
    width: 100%;
  }
  .leftcon
  {
    width: 100%;
    padding-top: 40px;
  }
  .contentWrap
  {
    width: 100%;
  }
  .xn
  {
    width: 100% !important;
  }
  .xn input
  {
    width: 100% !important;
  }
  .left-rest
  {
    font-size: 16px;
  }
  .cards
  {
    flex-direction: column;
  }
  .card
  {
    flex-direction: column;
    width: 100%;
    padding: 30px 0;
  }
  .imgcon
  {
    width: 100%;
  }
  .textcon
  {
    width: auto !important;
  }
  .contactGrid
  {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .footcontainer
  {
    display: flex;
    flex-direction: column;
  }
  .footer-grid
  {
    padding: 20px 0;
  }
  .textWrapper
  {
    width: 100% !important;
  }
  .topnav
  {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}